  #canvas-container {
    height: 100%;
    width: 100%;
  }
  
  h1 {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: rgb(12, 18, 112);
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    font-size: 55px;
    letter-spacing: 1px;
  }
  
  a {
    position: absolute;
    top: 77%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    border: none;
    background: #fff;
    cursor: pointer;
    transition: all 0.2s;
    padding: 10px 20px;
  }
  
  a:hover {
    color: #fff;
    background: #000;
  }